/* eslint-disable no-console */
import * as types from '../constants/actionTypes';
import operatorsApi from '../api/mockOperatorsAPI';


function fetchOperatorsBegin() {
  return {
    type: types.REQUEST_OPERATORS
  };
}

function receiveOperators(data) {
  return {
    type: types.RECEIVE_OPERATORS,
    data
  };
}

function beginPaymentRequest() {
  return {
    type: types.PAYMENT_REQUEST
  };
}

function endPaymentRequest(dispatch, type, data) {
  return dispatch({
    type: type,
    paymentData: {...data}
  });
}

export function loadOperators() {
  return (dispatch) => {
    dispatch(fetchOperatorsBegin());

    return operatorsApi.getOperators()
      .then(
        data => dispatch(receiveOperators(data)),
        (error) => {console.log('An error occurred.', error)}
      )
  };
}

export function proceedPayment(paymentData) {
  return (dispatch) => {
    dispatch(beginPaymentRequest());

    // imitate random API response
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        Math.random() >= 0.5
          ? resolve(types.PAYMENT_SUCCESS)
          : reject(types.PAYMENT_FAILURE);
      }, 1500);
    })
    .then(type => endPaymentRequest(dispatch, type, paymentData))
    .catch((error) => {
      endPaymentRequest(dispatch, error, paymentData);
      throw(error);
    });
  };
}
