/* eslint-disable no-console */
import React from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as actions from '../../actions/paymentActions';

import PaymentForm from '../PaymentForm/PaymentForm';
import OperatorItem from '../OperatorsSection/OperatorItem';


export class PaymentPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      paymentError: false
    };
  }

  proceedPayment = (paymentData) => {
    if (this.state.paymentError) this.setState({ paymentError: false });
    const operatorCode = this.props.operatorCode;
    this.props.actions.proceedPayment({...paymentData, operatorCode})
      .then(() => {this.props.history.replace('/')})
      .catch(() => {this.setState({ paymentError: true })});
  }

  render() {
    const {operatorCode, isFetch} = this.props;

    return (
      <div className="container">
        <OperatorItem
          code={operatorCode}
          />
        <PaymentForm
          isFetch={isFetch}
          paymentError={this.state.paymentError}
          proceedPayment={this.proceedPayment}
          />
      </div>
    );
  }
}

PaymentPage.propTypes = {
  actions: PropTypes.object.isRequired,
  operatorCode: PropTypes.string.isRequired,
  isFetch: PropTypes.bool.isRequired,
  history: PropTypes.object.isRequired,
};

function mapStateToProps(state, ownProps) {
  return {
    operatorCode: ownProps.match.params.operatorCode,
    isFetch: state.payment.isFetch
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps
)(PaymentPage));
