import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as actions from '../../actions/paymentActions';

import OperatorsSection from '../OperatorsSection';
import Loader from '../common/Loader';

export class HomePage extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    this.props.actions.loadOperators();
  }

  render() {
    const {operators, isFetch} = this.props;

    return (
      <div>
        <h1>Payment Terminal</h1>
        <h2>Choose operator</h2>
        {isFetch ? <Loader /> : <OperatorsSection operators={operators} />}
      </div>
    );
  }
}

HomePage.propTypes = {
  actions: PropTypes.object.isRequired,
  operators: PropTypes.array.isRequired,
  isFetch: PropTypes.bool.isRequired,
};

function mapStateToProps(state) {
  return {
    operators: state.payment.operators,
    isFetch: state.payment.isFetch,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(actions, dispatch)
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HomePage);
