/* eslint-disable no-console */
import React from 'react';
import {func, bool} from 'prop-types';
import InputMask from 'react-input-mask';

import TextInput from '../common/TextInput';
import {errorMessage} from '../../constants/messages';


class PaymentForm extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      accountNumber: '+7',
      paymentAmount: '',
      errors: {}
    };
  }

  onChange = ({target}) => {
    const {name, value} = target;

    if (name === 'paymentAmount') {
      // value = Math.min(target.max, Math.max(target.min, value));
      if (value.length > 0 && (value < 1 || value > 1000)) return false;
    }

    this.setState({[name]: value});
  }

  handleSubmit = (e) => {
    e.preventDefault();
    const errors = {};
    const {accountNumber, paymentAmount} = this.state;

    if (accountNumber.length < 16) {
      errors.accountNumber = true;
    }
    if (paymentAmount < 1 || paymentAmount > 1000) {
      errors.paymentAmount = true;
    }

    this.setState({errors});

    if (Object.keys(errors).length === 0) {
      this.props.proceedPayment({accountNumber, paymentAmount});
    }
  }

  render() {
    const {accountNumber, paymentAmount, errors} = this.state;
    const {isFetch, paymentError} = this.props;

    return (
      <form className="payment-form" onSubmit={this.handleSubmit}>
        <InputMask
          mask="+7 999 999 99 99"
          maskChar={null}
          value={accountNumber}
          onChange={this.onChange}>
            {(inputProps) => (
              <TextInput
                {...inputProps}
                className={errors.accountNumber ? "error" : ""}
                id="accountNumber"
                type="text"
                name="accountNumber"
                label="Номер телефона"
                info="Например, +7 123 456 78 90"
              />
            )}
        </InputMask>

        <TextInput
          className={errors.paymentAmount ? "error" : ""}
          id="paymentAmount"
          type="number"
          name="paymentAmount"
          label="Сумма"
          placeholder="руб"
          info="1,00 - 1000,00 руб"
          max="1000"
          min="1"
          value={paymentAmount}
          onChange={this.onChange}
        />

        <button
          type='submit'
          disabled={isFetch}>
            {isFetch ? 'Запрос ...' : 'Оплатить'}
        </button>

        {paymentError
          ? <span>{errorMessage}</span>
          : null}
      </form>
    );
  }
}

PaymentForm.propTypes = {
  proceedPayment: func.isRequired,
  paymentError: bool.isRequired,
  isFetch: bool.isRequired,
};

export default PaymentForm;
