import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';


const OperatorItem = ({ code, name }) => {
  const Logo = require(`../../assets/logos/${code.toLowerCase()}.png`);

  return (
    <Link to={`/payments/${code}`}
      className="operator-item"
      title={`Пополнить счет ${name}`}>
        <div className="logo">
          <div className="logo-image" style={{backgroundImage: `url(${Logo})`}}/>
        </div>
        {name ? <span>{name}</span> : null}
    </Link>
  );
};

OperatorItem.propTypes = {
  code: PropTypes.string.isRequired,
  name: PropTypes.string,
};

export default OperatorItem;
