/* eslint-disable no-console */
import {REQUEST_OPERATORS, RECEIVE_OPERATORS, PAYMENT_REQUEST,
  PAYMENT_SUCCESS,
  PAYMENT_FAILURE} from '../constants/actionTypes';
import initialState from './initialState';
import {getFormattedDateTime} from '../utils/dates';


export default function paymentReducer(state = initialState.payment, action) {
  switch (action.type) {
    case REQUEST_OPERATORS:
      return {
        ...state,
        isFetch: true
      };

    case RECEIVE_OPERATORS:
      return {
        ...state,
        operators: action.data,
        isFetch: false,
      };

    case PAYMENT_REQUEST:
      return {
        ...state,
        paymentData: {
          operatorCode: '',
          accountNumber: '',
          paymentDate: null,
          paymentAmount: '',
          paymentResult: '',
        },
        isFetch: true
      };

    case PAYMENT_SUCCESS:
      return {
        ...state,
        isFetch: false,
        paymentData: {
          ...action.paymentData,
          paymentDate: getFormattedDateTime(),
          paymentResult: 'SUCCESS',
        }
      };

    case PAYMENT_FAILURE:
      return {
        ...state,
        isFetch: false,
        paymentData: {
          ...action.paymentData,
          paymentDate: getFormattedDateTime(),
          paymentResult: 'FAILURE',
        }
      };

    default:
      return state;
  }
}
