import React from 'react';
import { string, func, number, oneOfType } from 'prop-types';

const TextInput = ({
  id,
  type,
  name,
  label,
  info,
  value,
  placeholder,
  onChange,
  ...otherProps
}) => (
  <label>
    <span>{label}</span>
    <div className="input-field">
      <input
        id={id}
        name={name}
        type={type}
        placeholder={placeholder}
        autoComplete="off"
        value={value}
        onChange={onChange}
        {...otherProps}
        />
      <div className="info">{info}</div>
    </div>
  </label>
);

TextInput.propTypes = {
  name: string.isRequired,
  label: string.isRequired,
  type: string.isRequired,
  info: string,
  placeholder: string,
  onChange: func,
  id: string,
  value: oneOfType([
    string,
    number
  ])
};

export default TextInput;
